const elevators = () => {
  let ElevatorModel;
  let modelView;

  ElevatorModel = (() => {
    function ElevatorModelView() {
      // construct for ElevatorModelView object with 2 properties: number of floors and array of elevators
      this.floors = 8;
      this.elevators = [
        {
          floor: 1,
          moving: false,
          waiting: false,
        }, {
          floor: 1,
          moving: false,
          waiting: false,
        },
      ];

      // me = elevatorModelView object
      let me = this;

      // create the button divs and timer for all the floor
      const buttons = (function () {
        const results = [];
        // for each floor, create 2 buttons (up/down)
        for (let floor = 8; floor >= 1; floor += -1) {
          results.push(
            `<div id = 'button-floor-${floor}' class='button-floor'>\n`

            + `<button class='button up' data-floor='${floor}' >`
            + ' <div class=\'up\'></div>'
            + '</button>\n'

            + '<p class="timer">0</p>\n'

            + `<button class='button down' data-floor='${floor}'>`
            + '<div class=\'down\'></div>'
            + '</button>\n'

            + '</div>'
          );
        }
        return results;
      }()).join('');
      // append the results array (contains all the button divs for each floor) to html
      $('#buttons')
        .empty()
        .append($(buttons))
        // adds class name 'on' when button is activated
        .off('click')
        .on('click', 'button', function () {
          // this value = <button>
          if ($(this)
            .hasClass('on')) {
            return;
          }
          $(this)
            .toggleClass('on');
          // when button is clicked ->
          return $(me)
            .trigger('pressed', [
              {
                floor: parseInt($(this)[0].dataset.floor),
                dir: $(this)
                  .children()
                  .hasClass('up') ? 'up' : 'down',
              },
            ]);
        });
    }

    ElevatorModelView.prototype.clearButton = function (floor, dir) {
      // beep when elevator arrives
      const snd = new Audio('data:audio/wav;base64,');
      snd.play();
      // remove 'on' class name when elevator arrives
      return $(`#button-floor-${floor} > button > div.${dir}`)
        .parent()
        .removeClass('on');
    };

    ElevatorModelView.prototype.closestIdleCar = function closestCar(floor) {
      console.log(`Finding closest car to ${floor} from `, this.elevators);
      // return an array of the non-moving elevators and their distance from the floor. example- [[el1, 3], [el2, 1]]
      const availableElevator = (function () {
        const freeElevators = [];
        const { elevators } = this;
        let car;
        for (let i = 0; i < 2; i += 1) {
          car = elevators[i];
          if (!car.moving && !car.waiting) {
            freeElevators.push([i + 1, Math.abs(floor - car.floor)]);
          }
        }
        return freeElevators;
      }).call(this);
      // if there are no available elevators
      if (!availableElevator) {
        return [];
      }
      if (availableElevator.length <= 1) {
        return availableElevator[0];
      }
      // returns an array of a single closest elevator and it distance
      return availableElevator[0][1] <= availableElevator[1][1] ? availableElevator[0] : availableElevator[1];

      //console.log(`Closest car to ${floor} is ${closest} from ${availableElevator}`);
    };

    ElevatorModelView.prototype.moveCar = function (carArr, floor) {

      let car = carArr[0];
      let deferred;
      let myCars;
      myCars = this.elevators;
      const duration = 500 * Math.abs(myCars[car - 1].floor - floor);
      deferred = $.Deferred();
      if (this.elevators[car - 1].moving) {
        return deferred.reject();
      }
      if (floor < 1 || floor > this.floors) {
        return deferred.reject();
      }
      // moving elevator animation
      this.elevators[car - 1].moving = true;
      $(`#elevator${car} .car`)
        .animate({
          bottom: `${(floor - 1) * 46}px`,
        }, {
          duration: duration * 1.5,
          easing: 'swing',
          complete() {
            myCars[car - 1].floor = floor;
            myCars[car - 1].moving = false;
            // make elevator wait for 2 seconds after reaching destination
            myCars[car - 1].waiting = true;
            console.log(`elevator ${car} is waiting`);
            var elevatorWait = setInterval(() => {
              console.log(`elevator ${car} FINISHED waiting`);
              myCars[car - 1].waiting = false;
              clearInterval(elevatorWait);
            }, 2000);
            return deferred.resolve();
          },
        })
        .delay(75);
      $(`#elevator${car} .car > .floorContainer`)
        .animate({
          top: `${-368 + floor * 46}px`,
        }, {
          duration: duration * 1.5,
          easing: 'swing',
        })
        .delay(75);

      // Timer handler - counting down in intervals of 0.5 second
      const numFloors = this.floors;
      let waitingTime = duration / 1000;
      var waitingCounter = setInterval(() => {
        document.getElementsByClassName('timer')
          .item(numFloors - floor).innerHTML = waitingTime;
        waitingTime -= 0.5;
        if (waitingTime < 0) {
          clearInterval(waitingCounter);
        }
      }, 500);

      return deferred;
    };

    return ElevatorModelView;
  })();

  // new instance of an Elevator model. has this functions in proto: clearButton, closestIdleCar, moveCar
  modelView = new ElevatorModel();
  $(modelView)
    .on('pressed', (e, _arg) => {
      let floor = _arg.floor, dir = _arg.dir;
      console.log(`Pressed ${floor}-${dir}`);
      let closestFloor = modelView.closestIdleCar(floor);
      // true if there is an available elevator
      if (closestFloor) {
        return modelView.moveCar(closestFloor, floor)
          .then(() => modelView.clearButton(floor, dir));
        // no avaiable elevators
      } else {
        document.querySelectorAll('[data-floor="' + floor + '"]')[0].classList.remove('on');
        document.querySelectorAll('[data-floor="' + floor + '"]')[1].classList.remove('on');
        alert('No free elevators, please wait...');
      }

    });

  // initial elevators positioning
  modelView.moveCar([1, 0], 4);
  modelView.moveCar([2, 0], 1);
};

elevators.call(this);
